:root {
  --vg-shaded-region-opacity: 0.2;
  --vg-asymptote-color: #777777;
  --vg-axis-color: #222222;
  --vg-grid-color: #888888;
  --vg-axis-width: 0.3mm;
  --vg-grid-width: 0.1mm;
  --vg-asymptote-width: 0.7mm;

  --vg-plot-1-path-color: #ff2600;
  --vg-plot-2-path-color: #0044cc;
  --vg-plot-3-path-color: #009955;
  --vg-plot-path-width: 0.5mm;

  --vg-geom-path-color: #009955;
  --vg-geom-path-width: 0.5mm;

  /* defaults */
  --vg-path-width: 0.5mm;
  --vg-path-color-1: black;
  --vg-path-color-2: black;
  --vg-path-color-3: black;
  --vg-path-linecap: none;
  --vg-path-linejoin: round;
  --vg-path-color: var(--vg-path-color-1);
  --vg-path-stroke-opacity: 1;
  --vg-fillpattern: none;
  --vg-path-fill: none;
  --vg-path-fillpattern-1: none;
  --vg-path-fillpattern-2: none;
  --vg-path-fillpattern-3: none;
  --vg-path-fill-opacity: var(--vg-shaded-region-opacity);
}


/*
.vg-cartesian-plane {}
*/

.vg-path {
  stroke: var(--vg-path-color);
  stroke-opacity: var(--vg-path-stroke-opacity);
  stroke-width: var(--vg-path-width);
  stroke-linecap: var(--vg-path-linecap);
  stroke-linejoin: var(--vg-path-linejoin);
  fill: var(--vg-path-fill);
  fill-opacity: var(--vg-path-fill-opacity);
  marker-start: var(--vg-path-marker-start, none);
  marker-mid: var(--vg-path-marker-mid, none);
  marker-end: var(--vg-path-marker-end, none);
}

.vg-marker {
  stroke: none; 
  fill: var(--vg-path-color);
  fill-opacity: var(--vg-path-stroke-opacity);
}
.vg-marker-hole { fill: white }

.vg-scatterplot {
  marker-start: var(--vg-marker-point);
  marker-mid: var(--vg-marker-point);
  marker-end: var(--vg-marker-point);
}

.vg-geomsegment {
  marker-start: var(--vg-marker-point);
  marker-mid: none;
  marker-end: var(--vg-marker-point);
}

.vg-geomray {
  marker-start: var(--vg-marker-point);
  marker-mid: none;
  marker-end: var(--vg-marker-arrowf);
}

.vg-geomline {
  marker-start: var(--vg-marker-arrowr);
  marker-mid: none;
  marker-end: var(--vg-marker-arrowf);
}

.vg-polygon {
  marker-start: var(--vg-marker-point);
  marker-mid: var(--vg-marker-point);
  marker-end: var(--vg-marker-point);
}

.vg-point {
  stroke: var(--vg-path-color);
  stroke-width: var(--vg-path-width);
  marker-start: var(--vg-marker-bigpoint);
}

.vg-hole {
  stroke: var(--vg-path-color);
  stroke-width: var(--vg-path-width);
  marker-start: var(--vg-marker-hole);
}

/*
.vg-path-marker {
  stroke: var(--vg-path-color);
  fill: var(--vg-path-color);
}
*/



.vg-axis {
  --vg-marker-point: url(#vg-marker-axis-point);
  --vg-marker-bigpoint: url(#vg-marker-axis-bigpoint);
  --vg-marker-arrow: url(#vg-marker-axis-arrow);
  --vg-marker-arrowr: url(#vg-marker-axis-arrowr);
  --vg-marker-arrowf: url(#vg-marker-axis-arrowf);
  --vg-marker-hole: url(#vg-marker-axis-hole);
  stroke: var(--vg-axis-color);
  stroke-width: var(--vg-axis-width);
  stroke-opacity: 1;
  stroke-linecap: square;  
}

.vg-axis-neg {
  --vg-path-marker-start: none;
  --vg-path-marker-mid: none;
  --vg-path-marker-end: none;
}

.vg-axis-pos {
  --vg-path-marker-start: none;
  --vg-path-marker-mid: none;
  --vg-path-marker-end: var(--vg-marker-arrowf);
}

.vg-grid {
  --vg-path-color: var(--vg-grid-color);
  --vg-path-width: var(--vg-grid-width);
  --vg-marker-point: url(#vg-marker-grid-point);
  --vg-marker-bigpoint: url(#vg-marker-grid-bigpoint);
  --vg-marker-arrow: url(#vg-marker-grid-arrow);
  --vg-marker-arrowr: url(#vg-marker-grid-arrowr);
  --vg-marker-arrowf: url(#vg-marker-grid-arrowf);
  --vg-marker-hole: url(#vg-marker-grid-hole);
  --vg-path-marker-mid: none;
  --vg-path-marker-end: none;
  --vg-path-marker-start: none;
  stroke-linecap: square;
}

.vg-asymptote {
  --vg-path-color: var(--vg-asymptote-color);
  --vg-path-width: var(--vg-asymptote-width);
  --vg-marker-point: url(#vg-marker-asymptote-point);
  --vg-marker-bigpoint: url(#vg-marker-asymptote-bigpoint);
  --vg-marker-arrow: url(#vg-marker-asymptote-arrow);
  --vg-marker-arrowr: url(#vg-marker-asymptote-arrowr);
  --vg-marker-arrowf: url(#vg-marker-asymptote-arrowf);
  --vg-marker-hole: url(#vg-marker-asymptote-hole);
  --vg-path-marker-mid: none;
  --vg-path-marker-end: none;
  --vg-path-marker-start: none;
  
  stroke-dasharray: 2mm 1mm;
  stroke-linecap: none;
  --vg-path-linecap: none;
}

.vg-shaded-region {
  --vg-path-width: 0;
  --vg-path-opacity: var(--vg-shaded-region-opacity);
  --vg-path-fill-opacity: var(--vg-shaded-region-opacity);
  --vg-path-fill: var(--vg-fillpattern-diagonal1);
}

.vg-secondary .vg-shaded-region {
  --vg-path-fill: var(--vg-fillpattern-diagonal2);
}

.vg-polygon {
  marker-start: var(--vg-marker-point);
  marker-mid: var(--vg-marker-point);
  marker-end: var(--vg-marker-point);
}

/* STYLES */


.vg-style-xyplot {
  --vg-path-color-1: var(--vg-plot-1-path-color);
  --vg-path-color-2: var(--vg-plot-2-path-color);
  --vg-path-color-3: var(--vg-plot-3-path-color);
  --vg-path-fillpattern-1: var(--vg-fillpattern-circle1);
  --vg-path-fillpattern-2: var(--vg-fillpattern-circle2);
  --vg-path-fillpattern-3: var(--vg-fillpattern-circle3);
  --vg-path-width: var(--vg-plot-path-width);
  --vg-path-color: var(--vg-path-color-1);
}

.vg-secondary {
  --vg-path-color: var(--vg-path-color-2);
}

.vg-tertiary {
  --vg-path-color: var(--vg-path-color-3);
}

.vg-style-geom {
  --vg-path-color: var(--vg-geom-path-color);
  --vg-path-width: var(--vg-geom-path-width);
}

